import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { NavLink, useNavigate } from 'react-router-dom';
import Logo from '../assets/logo1.png';

export const NavData = [
  {
      title: "Home",
      path: "/",
    },
    {
      title: "About",
      path: "/about",
    },
    {
      title: "Product & Services",
      path: "/product",
    },
    {
      title: "Career",
      path: "/career",
    },
    {
      title: "Contact Us",
      path: "/contact",
    },
]

function Navigationbar() {

  const navigate = useNavigate();
  const scrollToTop = () => {
    window.scrollTo({
        top: 0,
        behavior: 'smooth',
    });
}

  return (
    <div>
      <Navbar fixed="top" bg="primary" variant="dark" className="mb-1 navBarStyle">
          <Container fluid>
            <Navbar.Brand style={{marginLeft: '60px', lineHeight: '45px', fontSize: '25px', cursor: 'pointer'}} onClick={()=>{scrollToTop();navigate('/');}}><img
              src={Logo}
              width="45"
              height="45"
              className="d-inline-block align-top"
              alt="React Bootstrap logo"
            />{'  '}
             &nbsp;Bloomwealth Brand Equity
            </Navbar.Brand>
            <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-md`} />
            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-md`}
              aria-labelledby={`offcanvasNavbarLabel-expand-md`}
              placement="end"
              style={{marginRight: '60px'}}
            >
              <Offcanvas.Header closeButton>
                <Offcanvas.Title id={`offcanvasNavbarLabel-expand-md`}>
                  Offcanvas
                </Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav className="justify-content-end flex-grow-1 pe-3">
                  {NavData.map((item, index) => {
                      return (
                      <Nav.Link style={{marginRight: '10px'}}>
                         <NavLink
                          key={index}
                          to={item.path}
                          className={'unformattedNavLink'}>
                          <Nav.Item>{item.title}</Nav.Item>
                        </NavLink>
                      </Nav.Link>
                     )
                  })}
                  {/* <NavLink to="/about">About</NavLink>
                  <NavLink to="/product">Products & Services</NavLink>
                  <NavLink to="/career">Career</NavLink>
                  <NavLink to="/contact">Contact</NavLink> */}
                </Nav>
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </Container>
        </Navbar>
    </div>
  )
}

export default Navigationbar;